:root {
  color: white;
}

a:link {
  text-decoration: none;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* default is mobile */
.nav{
  text-decoration: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
}

.nav-logo{
  grid-column: 1 / 2;
  padding: 5% 15% 0% 15%;
  align-self: center;
}

.nav-buttons{
  grid-column: 2 / 2;
  padding: 10% 28% 0px 5%;
}


.nav button{
  border: steelblue 10px;
  margin: 5%;
  width: 100%;
  padding: 2px;
  border-radius: 18px;
  box-shadow: 10px 10px;
}

/* Page Heading */
heading {
  text-align: center;
  font-size: 30px;
  padding: 20px;
  font-weight: 700;
}

/* classess for the front page */
.introduction {
  text-align: center;
  padding: 30px;
}

.introduction-buttons {
  margin: 30px;
  text-align: center;
  padding: 2px;
  display: flex;
  flex-direction: row;
  border-radius: 18px;
  justify-content: space-evenly;
}


.introduction button {
  align-self: center;
  text-align: center;
  padding: 2px;

  width: 100px;
  border-radius: 18px;
  box-shadow: 10px 10px;
}

/* standard-form */

.standard-form form {
  display: grid;
  grid-template-columns: 1fr;
}

.standard-form input {
  margin: 10px;
  grid-column: 1;
  width: 90%;
  padding: 2px;
  border-radius: 18px;
}

.standard-form button {
  margin: 10px;
  padding: 2px;
  width: 40%;
  border-radius: 18px;
  box-shadow: 10px 10px;
  justify-self: center;
}

.standard-form label {
  display: flex;
  flex-direction: row;
  margin: 10px;
  padding: 2px;
  justify-self: center;
}


.standard-form textarea {
  grid-column: 1;
  margin: 10px;
  height: 150px;
  width: 90%;
  padding: 2px;
  resize: none;
  border-radius: 18px;
}

/* My Account */

.my-account {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0 30px;
}

.my-account input {
  grid-column: 1;
  width: 90%;
  padding: 2px;
  grid-column: 1 / 2;
  border-radius: 18px;
}

.edit-my-account
{
  text-align: center
}

#new-post a {
  margin: 10px auto;
}

.edit-my-account button {
  margin: 10px auto;
  padding: 2px;
  width: 70%;
  border-radius: 18px;
  box-shadow: 10px 10px;
  justify-content: center;
}

.my-account button {
  grid-column: 2 / 3;
  margin: 10px;
  padding: 2px;
  width: 90%;
  border-radius: 18px;
  box-shadow: 10px 10px;
  justify-self: center;
}

.my-account label {
  grid-column: 1 / 2;
  margin: 10px;
  padding: 2px;
  justify-self: center;
}

.my-account textarea {
  grid-column: 1;
  margin: 10px;
  height: 150px;
  width: 90%;
  padding: 2px;
  resize: none;
  border-radius: 18px;
}

/* deep-dive */
.deep-dive{
  text-align: left;
  padding: 0 30px;
}
.deep-dive button {
