margin: 12px;
}
.category-select button {
grid-column: 3;
margin: 10px;
padding: 2px;
width: 40%;
border-radius: 18px;
box-shadow: 10px 10px;
justify-self: left;
}
.category-form-select {
position: relative;
grid-column: 2 / 3;
}

.category-form-select select {
display: none; 
}

.select-selected {
background-color: white;
}


.category-select label {
grid-column: 1 / 2;
color: white;

justify-self: right;
margin: 10px;

}
/* New Posts Page   */
.new-post-base {
display: grid;
grid-template-columns: 200px 1fr;
}

.new-post-base button {
grid-column: 2 / 3;
margin: 10px;
padding: 2px;
width: 40%;
border-radius: 18px;
box-shadow: 10px 10px;
justify-self: center;
}

.new-post-base input {
margin: 10px 0;
grid-column: 2 / 3;
width: 40%;
padding: 2px;
border-radius: 18px;
height: 30px;
}

.new-post {
margin: 0 50px;
display: grid;
grid-template-columns: 1fr;
}

.new-post label {
font-size: 20px;
font-weight:700;
padding: 0;
text-align: center;
}
.new-post h2 {
text-align: center;
font-size: 30px;
font-weight: 700;
align-self: left;
}

.new-post-base label{
margin: 2px;
position: relative;
grid-column: 1 / 2;
}


.new-post-base-select {
position: relative;
grid-column: 2 / 3;
height:20px;
line-height: 80%;
margin-top: 40px;
}

}
